import React, { useState, useEffect } from 'react';
import { DateInput, Box, Select, Button  } from 'grommet';
import { parseISO, format, setMinutes, setHours } from 'date-fns';

const DateTimePicker = ({ dateObject, selectDate, onClick }) => {
    const [useDateObject, setDateObject] = useState(dateObject || new Date())
    const [date, setDate] = useState(useDateObject.toISOString());
    const [hour, setHour] = useState(format(useDateObject, 'H'))
    const [min, setMin] = useState(format(useDateObject, 'm'))
    console.log(dateObject)
    useEffect(() => {
        let dateObj = parseISO(date);
        dateObj = setHours(dateObj, hour)
        dateObj = setMinutes(dateObj, min)
        selectDate && selectDate(dateObj)
        setDateObject(dateObj)
    }, [hour, min, date])

    return <><Box
            direction="row-responsive"
            pad={{
                bottom: "small"
            }}
            >
        <DateInput
            format="mm/dd/yyyy"
            value={date}
            onChange={({ value }) => { setDate(value) }}
        />

        <Select
            options={Object.keys([...Array(24).keys()])}
            value={hour}
            onChange={({ option }) => setHour(option)}
        />

        <Select
            options={Object.keys([...Array(60).keys()])}
            value={min}
            onChange={({ option }) => setMin(option)}
        />
    </Box>
        <Box align="end">
        <Button 
            onClick={() => onClick && onClick(useDateObject)}
            label="Get Chart"
        />
        </Box>
    </>;
}

export default DateTimePicker;